import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
let {
	log
} = console;
const {
	navBar,
	imgRemote,
	appid,
	appsecret
} = app;
export default {
	data() {
		return {
			navBar: navBar,
			imgRemote: imgRemote,
			loading:true,
			config: [],
			list: [],
			page: 1,
			num: 10,
			arrObj: [],
			index: '',
			bitmap:true,
			cartware: []
		};
	},
	methods: {
		closeCart(){
			this.$refs.addcart.onClose();
		},
		closeKey(){
			this.$refs.keyboard.cancel();
		},
		leftClick(e) {
			uni.navigateBack({
				delta: 1
			});
		},
		toParent(e) {
			let item = e.arrObj;
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp,
				item_id: item.id,
				attr_id: 0,
				item_num: e.val
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
					sign: sign
				},
				obj
			);
			rs.postRequests('changeNum', params, res => {
				let data = res.data;
				if (data.code == 200) {
					uni.showToast({
						title: '加入购物车成功',
						icon: 'none',
						duration: 2000
					})
					this.list[this.index].cart_num = e.val;
				} else if (data.code == 407 || data.code == 406) {
					rs.Toast("购买数量不能超过活动数量")
				} else {
					rs.Toast(res.data.msg)
				}
			});
			this.$refs.popup.close();
		},
		getIndexSelect() {
			let {
				page,
				num,
				list
			} = this;
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
					sign: sign,
					page: page,
					num: num
				},
				obj
			);
			rs.getRequests('itemCommon', params, res => {
				let data = res.data;
				if (data.code == 200) {
					this.config = data.data;

					if (data.data.list.length == 0) {
						this.list = [];
						this.bitmap = false;
					} else {
						this.list = data.data.list;
						if (data.data.list.length < 10) {
							this.loading = false;
						} else {
							this.loading = true;
						}
					}
				}
			});
		},
		openCart(item) {
			this.cartware = item;
			this.$refs.cart.open();
		},
		onClose() {
			this.$refs.cart.close();
		},
		// 显示键盘
		showKey(item, index) {
			this.arrObj = item;
			this.index = index;
			this.$refs.popup.open();
		}
	},
	onHide(){
		// uni.setStorageSync('recommed',);
		uni.setStorageSync('recommed',this.list);
		console.log(this.list)
	},
	onShow() {
		console.log(this.page)
		if (this.page==1) {
			this.getIndexSelect();
		}else{
			
			this.list=uni.getStorageSync('recommed');
			// console.log(this.list);
		}

	},
	onReachBottom() {
		let {
			page,
			num,
			list
		} = this;
		let timeStamp = Math.round(new Date().getTime() / 1000);
		let obj = {
			appid: appid,
			timeStamp: timeStamp
		};
		let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
		let params = Object.assign({
				sign: sign,
				page: page + 1,
				num: num
			},
			obj
		);
		rs.getRequests('itemCommon', params, res => {
			if (res.data.code == 200) {
				if (res.data.data.list.length != 0) {
					this.list.push(...res.data.data.list);
					this.page += 1;
					this.loading = true;
				} else {
					this.loading = false;
				}
			}
		});
	}
};
